:root {
    --primary-color: #295FF4;
    --primary-glass-color: rgba(41, 95, 244, 0.5);
    --adm-color-primary: var(--primary-color) !important;
    --font-size-mini: 10px;
    --font-size-small: 16px;
    --font-size-mid: 20px;
    --font-size-big: 30px;
    --font-size-big1: 50px;
    --border-radius: 25px;
    --backdrop-filter-1: blur(5px);
    --backdrop-filter-2: blur(20px);
    --success-color: #64C466;
    --warn-color: #F6CE46;
    --danger-color: #EA4D3D;
    --money-color: #FFD700;
    --mid-zindex: 50;
    --max-zindex: 100;
    --box-shadow: 2px 4px 12px var(--main-shadow-color);
    --primary-box-shadow: 2px 4px 12px var(--primary-glass-color);
    --box-active-shadow: 2px 4px 12px var(--primary-color);
}

:root[theme='day'] {
    --main-bg-color-1: #F2F2F7;
    --main-bg-color-2: #fff;
    --main-text-color-1: #000;
    --main-text-color-2: #9b9b9b;
    --main-shadow-color: rgba(0, 0, 0, .1);
    --main-glass-color: rgb(255, 255, 255, .1);
    --main-bg-image: url('../../img/dayBg.jpg') no-repeat;
    --system-head-color: #67615c;
}

:root[theme='dark'] {
    --main-bg-color-1: #000;
    --main-bg-color-2: #161617;
    --main-text-color-1: white;
    --main-text-color-2: #9b9b9b;
    --main-shadow-color: rgb(255, 255, 255, .1);
    --main-glass-color: rgb(0, 0, 0, .2);
    --main-bg-image: url('../../img/nightBg.jpg') no-repeat;
    --system-head-color: #26201b;
}

body {
    background: var(--main-bg-color-1) !important;
    color: white !important;
    font-size: var(--font-size-small) !important;
    overflow: hidden;
}

/*antd*/
.adm-modal-body {
    background: var(--main-bg-color-2) !important;
}

.adm-auto-center-content {
    color: white !important;
}

.adm-toast-mask .adm-toast-main {
    background: var(--main-glass-color) !important;
    backdrop-filter: var(--backdrop-filter-1);
}

.adm-toast-main .adm-auto-center-content {
    color: white !important;
}

.adm-mask {
    background: var(--main-glass-color) !important;
    backdrop-filter: var(--backdrop-filter-1);
}

.adm-modal-content {
    color: white !important;
}

.adm-dialog-body, .adm-modal-body {
    background: var(--main-glass-color) !important;
    backdrop-filter: var(--backdrop-filter-1);
    box-shadow: var(--box-shadow);
    border-top: 1px solid var(--main-glass-color);
    border-left: 1px solid var(--main-glass-color);
}

.adm-button-primary {
    color: white;
}