/* feature-common */
.feature-list {
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
}

.feature-item-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.feature-item {
    flex-shrink: 0;
    backdrop-filter: var(--backdrop-filter-2);
    width: calc(100% - 20px);
    border-radius: var(--border-radius);
    border-top: 1px solid var(--main-glass-color);
    border-left: 1px solid var(--main-glass-color);
    box-shadow: var(--box-shadow);
    padding: 10px;
}

.feature-head {
    width: 100%;
    display: flex;
    margin-bottom: 10px;
}

.feature-head-left {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.feature-head-left-title {
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.feature-head-right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
    gap: 5px;
}

.feature-head-right-btn {
    font-size: var(--font-size-mid);
    font-weight: bold;;
    cursor: pointer;
    color: var(--warn-color);
}

.feature-content {
    width: 100%;
}

/* habbit */
.habit-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
}

.habit-item {
    position: relative;
    flex-shrink: 0;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
}

.habit-item-ok {
    position: absolute;
    top: 0;
    right: 25%;
    font-size: var(--font-size-big);
    text-align: center;
    line-height: 20px;
    color: var(--success-color);
    font-weight: bold;
}

.habit-item-icon {
    font-size: 50px;
    cursor: pointer;
}

.habit-item-time {
    font-size: var(--font-size-mini);;
    font-weight: bold;
    color: var(--warn-color);
}

.habit-item-title {
    font-size: var(--font-size-small);
    font-weight: bold;
}

.habit-item-stat {
    font-size: var(--font-size-mini);
}

.habit-item-operate {
    margin-top: 10px;
}

/*todayTodo*/
.todayTodo-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
}

.todayTodo-item {
    font-size: var(--font-size-mid);
    display: flex;
    flex-direction: row;
}

.todayTodo-item-info {
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    border-radius: var(--border-radius);
    padding: 10px;
    font-weight: bold;
    white-space: nowrap;
}

.todayTodo-item-title {
    height: 100%;
    font-size: var(--font-size-mid);;
}

.todayTodo-item-operate {
    width: 100%;
    height: 100%;
    margin-left: 10px;
    color: var(--main-text-color-2);
    font-size: var(--font-size-big);
    cursor: pointer;
}

.todayTodo-item-operate.ok {
    color: var(--success-color);
}

.todayTodo-item-icon {
    height: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
}

/*timer*/
.timer-list {
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow-x: auto;
    padding-bottom: 10px;
}

.timer-list-item {
    font-size: var(--font-size-mid);
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: row;
    padding: 10px;
    color: var(--warn-color);
    font-weight: bold;
    white-space: nowrap;
}

.timer-list-item-title {
    height: 100%;
}

.timer-list-item-time {
    height: 100%;
}

.timer-list-item-operate {
    margin-left: 10px;
    height: 100%;
}

/* env */
.env-location {
    margin-right: 10px;
    font-size: var(--font-size-mid);
    font-weight: bold;;
}

.env-temp-text {
}

.env-date {
    display: flex;
    flex-direction: column;
}

.env-time {
    text-align: center;
}

.env-time-info {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
}

.env-time {
    width: 33.3%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    font-size: 50px;
}

.env-status {
    width: 33.3%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    cursor: pointer;
}

.env-day {
    width: 33.3%;
    height: 100%;;
    display: flex;
    align-items: center;
    justify-content: right;
    font-size: 30px;
    color: var(--warn-color);
}

/* event */
.event-item-list {
    display: flex;
    flex-direction: row;
    overflow: auto;
    gap: 10px;
    padding-bottom: 10px;
}

.event-item {
    flex-shrink: 0;
    width: 250px;
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    border-radius: var(--border-radius);
    padding: 10px;
    margin-bottom: 10px;
}

.event-item-head {
    display: flex;
}

.event-type {
    width: 50%;
    justify-content: left;
    font-size: var(--font-size-big);
}

.event-time-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: right;
}

.event-date {
    display: flex;
    align-items: center;
    justify-content: right;
}

.event-time {
    display: flex;
    align-items: center;
    justify-content: right;
}

.event-item-content {
    display: flex;
    flex-direction: row;
    height: 100px;
}

.event-title {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-big);
}

.event-left-info {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;;
    font-size: var(--font-size-mid);
}

.event-left-day {
    font-size: var(--font-size-mid);
    font-weight: bold;
    text-align: center;
    color: var(--warn-color);
}

.event-item-operate {
    margin-left: 10px;
    font-size: var(--font-size-small);
}

/*note*/
.note-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
    padding-bottom: 10px;
}

.note-item {
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.note-info {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    max-width: 300px;
    padding: 10px;
    border-radius: var(--border-radius);
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    cursor: pointer;
}

.note-title {
    height: 40px;
    width: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;;
    white-space: nowrap;
    overflow: auto;
}

.note-time {
    width: 100%;
    font-size: var(--font-size-small);
    white-space: nowrap;
}

.markdown-view {
    position: fixed;
    left: 10px;
    right: 10px;
    top: 50px;
    bottom: 100px;
    z-index: var(--max-zindex);
    padding: 10px;
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
}

.markdown-view-title {
    height: 30px;
    width: 100%;
    margin-bottom: 10px;
    font-size: var(--font-size-big);
    font-weight: bold;;
}

.markdown-view-title input {
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    outline: none;
    background: rgb(0, 0, 0, .0);
    font-size: var(--font-size-big);;
    border-radius: 0;
}

.markdown-view-time-type {
    height: 20px;
    width: 100%;
    margin-bottom: 10px;
    font-size: var(--font-size-small);
    text-align: right;
}

.markdown-view-content {
    height: calc(100% - 40px - 30px - 50px);
    width: 100%;
    margin-bottom: 10px;
    overflow: auto;
    word-break: break-all;
    font-size: var(--font-size-mid);
}

.markdown-view-content textarea {
    width: calc(100% - 22px);
    height: calc(100% - 22px - 10px);
    padding: 10px;
    background: rgb(0, 0, 0, .0);
    border: 1px solid var(--main-glass-color);
    resize: none;
    font-size: var(--font-size-mid);
    color: white;
    outline: none;
}

.markdown-view-operate {
    height: 50px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.markdown-view-operate-btn {
    flex-shrink: 0;
    width: 33.3%;
    font-size: var(--font-size-big);
    cursor: pointer;
}

/*news*/
.news-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: auto;
    padding-bottom: 10px;
}

.news-item {
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
}

.news-item-time {
    font-size: var(--font-size-small);
    color: var(--warn-color);
}

.news-item-title {
    font-size: var(--font-size-big);
    font-weight: bold;;
    max-width: 200px;
    white-space: nowrap;
    overflow: auto;
}

/*wallet*/
.wallet {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.money-color {
    color: var(--money-color);
}

.out-money-color {
    color: var(--danger-color);
}

.wallet-left {
    flex-shrink: 0;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.wallet-out {
    height: 100px;
    width: 100%;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.wallet-out-title {
    text-align: center;
    font-size: var(--font-size-big);
}

.wallet-out-info {
    font-size: var(--font-size-big);
    text-align: center;
}

.out-money {
    flex-shrink: 0;
    font-size: var(--font-size-big);
    color: var(--danger-color);
}

.month-maybe-money {
    flex-shrink: 0;
    font-size: var(--font-size-big);
    background: rgba(0, 0, 0, 0);
    color: white;
}

.wallet-store {
    flex-shrink: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.month-maybe-store {
    width: 50%;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.year-maybe-store {
    width: 50%;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.wallet-right {
    flex-shrink: 0;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.wallet-record {
    height: 100px;
    overflow-y: auto;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.wallet-record-item {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
    font-size: var(--font-size-small);
}

.wallet-record-item-money {
    font-weight: bold;
    font-size: var(--font-size-mid);
}

.wallet-record-item-money.out {
    color: var(--danger-color);
}

.wallet-record-item-money.in {
    color: var(--money-color);
}

.wallet-info {
    display: flex;
    gap: 10px;
}

.total-out {
    width: 50%;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.total-money {
    width: 50%;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    align-items: center;
}