.assistant {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--main-bg-image);
    background-size: cover;
}

.assistant-head {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    width: calc(100% - 40px);
    z-index: var(--max-zindex);
}

/*head-logo*/
.assistant-head-logo {
    height: 40px;
    width: 40px;
    z-index: var(--max-zindex);
}

.assistant-head-logo img {
    height: 100%;
    width: 100%;
}

.assistant-main {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    padding-top: 50px;
    padding-bottom: 100px;
}

.assistant-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 90px;
    z-index: var(--max-zindex);
}

/*head-notification*/
.assistant-head-notification {
    height: 40px;
    max-width: calc(100% - 50px);
    margin-left: 10px;
}

.assistant-head-notification-item {
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    padding: 10px;
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    border-radius: var(--border-radius);
    font-weight: bold;
    white-space: nowrap;
    overflow-y: hidden;
}