/*askInput*/
.ask-input {
    height: calc(100% - 25px);
    width: calc(100% - 40px);
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}

.ask-send-input {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    border-top: 1px solid var(--main-glass-color);
    border-left: 1px solid var(--main-glass-color);
    box-shadow: var(--box-shadow);
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
}

.ask-left-btn {
    width: 30px;
    height: 100%;
    padding-left: 15px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    background: rgba(0, 0, 0, 0);;
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.ask-left-btn-empty {
    color: var(--main-text-color-2);
}

.ask-send-input input {
    width: calc(100% - 45px - 45px - 20px);
    height: 100%;
    padding: 0 10px;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, 0);
    font-size: var(--font-size-mid);
    border: 0;
    border-radius: 0;
    color: white;
}

.ask-right-btn {
    width: 30px;
    height: 100%;
    padding-right: 15px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    background: rgba(0, 0, 0, 0);
    color: var(--main-text-color-2);
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.ask-right-btn.ok {
}

/* chat-list */
.chat-record {
    position: fixed;
    bottom: 90px;
    left: 0;
    right: 0;
}

.chat-record-list {
    width: calc(100% - 40px);
    padding: 0 20px;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
}

.chat-record-list-item {
    flex-shrink: 0;
    height: 50px;
    padding-bottom: 10px;
}

.chat-record-list-content {
    height: 100%;
    padding: 0 10px;
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    font-weight: bold;
    cursor: pointer;
}

.chat-record-list-content-del {
    color: red;
    margin-left: 10px;
}

/*answer*/
.assistant-out {
    position: fixed;
    top: 60px;
    padding: 0 10px;
    width: calc(100% - 20px);
    z-index: var(--max-zindex);
    display: flex;
    justify-content: center;
}

.assistant-answer {
    max-width: calc(100% - 20px);
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    border-radius: var(--border-radius);
    padding: 10px;
    box-shadow: var(--box-shadow);
    transition: all 0.3s ease-in;
}

.assistant-answer-title {
    height: 35px;
    line-height: 35px;
    font-size: var(--font-size-mid);
    font-weight: bold;
    white-space: nowrap;
    overflow-x: auto;
}

.assistant-answer-content {
    max-height: calc(80vh - 60px - 35px - 120px);
    overflow: auto;
    word-break: break-all;
    font-size: var(--font-size-mid);
}