/*flex*/
.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

/*empty*/
.sys-empty {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main-text-color-2);
}

/* modal */
.modal-mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: var(--mid-zindex);
    backdrop-filter: var(--backdrop-filter-1);
}

/* animate */
.animate-rotate {
    animation: animate-rotate 1s linear infinite;
}

.animate-breath {
    animation: animate-shadow-breath 2s linear infinite;
}

.animate-biger-breath {
    animation: animate-biger-breath 2s linear infinite;
}

.animate-shadow-breath {
    animation: animate-shadow-breath 2s linear infinite;
}

.animate-popUp {
    animation: animate-popUp .3s linear;
}

.animate-popDown {
    animation: animate-popDown .3s linear;
}

.animate-scale-in {
    animation: animate-scale-in 0.5s ease-in-out forwards;
}

.animate-shrink-breath {
    animation: animate-shrink-breath 2s linear infinite;
}

@keyframes animate-shadow-breath {
    0% {
        box-shadow: var(--box-shadow);
    }

    50% {
        box-shadow: var(--box-active-shadow);
    }

    100% {
        box-shadow: var(--box-shadow);
    }
}

@keyframes animate-shrink-breath {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes animate-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes animate-scale-in {
    from {
        transform: scale(0.1);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes animate-popUp {
    from {
        opacity: 0;
        transform: translateY(50px);
        /* 从底部开始 */
    }

    to {
        opacity: 1;
        transform: translateY(0);
        /* 向上移动到最终位置 */
    }
}

@keyframes animate-popDown {
    from {
        opacity: 0;
        transform: translateY(-50px);
        /* 从顶部开始 */
    }

    to {
        opacity: 1;
        transform: translateY(0);
        /* 向上移动到最终位置 */
    }
}

@keyframes animate-biger-breath {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

/*operate*/
.operate-row {
    display: flex;
    flex-direction: row;
}

.operate-row-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin-left: 10px;
}

.del-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-small);
    color: var(--danger-color);
    cursor: pointer;
}

.timeCount {
    width: 100%;
}

div {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.btn:active {
    transform: scale(0.5);
}

/* 整体滚动条样式 */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* 滚动条轨道 */
::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    background: var(--main-text-color-2);
    border-radius: 4px;
}

/*notification*/
.main-notification {
    position: fixed;
    top: 50px;
    bottom: 100px;
    left: 10px;
    right: 10px;
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter-2);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    padding: 10px;
    z-index: var(--max-zindex);
}

.main-notification-info {
    padding-top: 30px;
    height: calc(100% - 30px - 130px);
    text-align: center;
    width: 100%;
    font-size: 50px;
    font-weight: bold;;
    overflow-y: auto;
}

.main-notification-time {
    height: 80px;
    line-height: 80px;
    text-align: center;
    width: 100%;
    font-size: 30px;
    font-weight: bold;
    color: var(--warn-color);
}

.main-notification-operate {
    height: 50px;
    line-height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.main-notification-operate-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-big);
    font-weight: bold;
    width: 50%;
}